import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import * as db from '@prisma/client-flowda'
import { UserError } from '../lib/error-code'
import * as bcrypt from 'bcrypt'
import type { JwtPayload } from 'jsonwebtoken'
import * as jwt from 'jsonwebtoken'
import { FLOWDA_ENV } from '../lib/flowda-env'
import { LoggerService, UnauthorizedException } from '@nestjs/common'
import dayjs from 'dayjs'
import {
  AccountExistsSchemaDto,
  FindByUnionIdAndTenantIdSchemaDto,
  GetTenantByNameSchemaDto,
  RegisterByUnionIdSchemaDto,
  RegisterDto,
  resetPasswordSchemaDto,
  verifyMobileSchemaDto,
} from './user.dto'
import { SmsClientSymbol } from '../symbols'
import type { Client } from 'tencentcloud-sdk-nodejs/tencentcloud/services/sms/v20210111/sms_client'

@injectable()
export class UserService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(SmsClientSymbol) private smsClient: Client,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserService.name)
  }

  async register(dto: RegisterDto) {
    const user = await this.prisma.user.findFirst({
      where: {
        username: dto.username,
      },
    })
    if (user) {
      this.logger.warn('User exist:' + dto.username)
      throw new UserError.UserExist()
    }
    const hashedPassword = await bcrypt.hash(dto.password, 10)
    const aUser = await this.prisma.user.create({
      data: {
        username: dto.username,
        hashedPassword: hashedPassword,
        hashedRefreshToken: null,
        tenantId: dto.tenantId,
      },
    })
    return {
      id: aUser.id,
      username: aUser.username,
    }
  }

  async resetPassword(reqUser: any, dto: resetPasswordSchemaDto) {
    const tenantRet = await this.prisma.tenant.findUniqueOrThrow({
      where: {
        id: reqUser.tid,
      },
    })
    if (tenantRet.name !== 'superadmin') {
      throw new Error('Not allowed')
    }

    await this.prisma.user.findFirstOrThrow({
      where: {
        id: dto.userId,
        tenantId: dto.tenantId,
      },
    })
    const hashedPassword = await bcrypt.hash(dto.password, 10)
    return this.prisma.user.update({
      where: {
        id: dto.userId,
      },
      data: {
        hashedPassword,
      },
    })
  }

  async validateByEmail(tenantId: number, email: string, password: string) {
    const userRet = await this.prisma.user.findFirstOrThrow({
      where: {
        email: email,
        tenantId: tenantId,
      },
    })
    return this.validate(userRet.username, password)
  }

  async validate(username: string, password: string) {
    const user = await this.prisma.user.findFirst({
      where: {
        username: username,
      },
      include: {
        tenant: true,
      },
    })

    if (!user) {
      throw new UnauthorizedException({ reason: 'User does not exist', username })
    }

    if (!user.hashedPassword) {
      throw new UnauthorizedException({ reason: 'Password is not initialized', username })
    }

    const match = await bcrypt.compare(password, user.hashedPassword)
    if (!match) {
      throw new UnauthorizedException({ reason: 'Username and password is not matched', username })
    }

    const payload = { uid: user.id, tid: user.tenantId }
    const rt = this.generateJwt(payload, FLOWDA_ENV.REFRESH_TOKEN_SECRET, FLOWDA_ENV.REFRESH_TOKEN_EXPIRE)
    await this.prisma.user.update({
      where: { id: user.id },
      data: {
        hashedRefreshToken: rt.token,
      },
    })

    const at = this.generateJwt(payload, FLOWDA_ENV.ACCESS_TOKEN_SECRET, FLOWDA_ENV.ACCESS_TOKEN_EXPIRE)

    this.logger.log(
      `validate pass, t: ${user.tenant.name}, u: ${user.username}, e: ${dayjs().add(
        FLOWDA_ENV.ACCESS_TOKEN_EXPIRE,
        's',
      )}`,
    )

    return {
      username: user.username,
      refresh_token: rt.token,
      access_token: at.token,
    }
  }

  generateJwt(payload: any, secret: string, expires: number) {
    const exp0 = Date.now() + expires * 1000
    const token = jwt.sign(
      {
        ...payload,
        ...{ exp0 },
      },
      secret,
      {
        expiresIn: `${expires}s`,
      },
    )
    const decode = jwt.decode(token) as JwtPayload
    return {
      token: token,
      iat: decode.iat as number,
      exp: decode.exp as number,
    }
  }

  verifyAccessToken(at: string) {
    return jwt.verify(at, FLOWDA_ENV.ACCESS_TOKEN_SECRET) as JwtPayload
  }

  getUserInfo(uid: number) {
    // todo: 后续可以放 profile
    return this.prisma.user.findUniqueOrThrow({
      where: {
        id: uid,
      },
      select: {
        id: true,
        username: true,
        unionid: true,
      },
    })
  }

  getTenantInfo(tid: number) {
    return this.prisma.tenant.findUniqueOrThrow({
      where: {
        id: tid,
      },
    })
  }

  async sendSmsVerifyCode(mobile: string) {
    const code = generateVerificationCode()
    this.logger.debug!(`[sendSmsVerifyCode] ${mobile} ${code}`)
    try {
      const res = await this.smsClient.SendSms({
        SmsSdkAppId: '1400886368',
        PhoneNumberSet: ['+86' + mobile],
        TemplateId: '2062585',
        SignName: '上海只冲网络科技有限公司',
        TemplateParamSet: [code],
      })
      this.logger.debug!('[sendSmsVerifyCode]')
      console.log(res)
    } catch (e: any) {
      throw new Error(e)
      console.error(e)
    }
    return this.prisma.sentSms.create({
      data: {
        mobile,
        code: generateVerificationCode(),
      },
    })
  }

  async verifyMobile(dto: verifyMobileSchemaDto) {
    const now = dayjs()
    const tenMinutesAgo = now.subtract(10, 'minute')
    await this.prisma.sentSms.findFirstOrThrow({
      where: {
        code: dto.code,
        mobile: dto.mobile,
        createdAt: {
          gte: tenMinutesAgo.toDate(),
          lte: now.toDate(),
        },
      },
    })
    await this.prisma.user.findFirstOrThrow({
      where: {
        id: dto.uid,
        tenantId: dto.tid,
      },
    })
    return this.prisma.user.update({
      where: {
        id: dto.uid,
      },
      data: {
        mobile: dto.mobile,
      },
      select: {
        id: true,
        tenantId: true,
      },
    })
  }

  getTenantByName(dto: GetTenantByNameSchemaDto) {
    return this.prisma.tenant.findFirstOrThrow({
      where: {
        name: dto.tenantName,
      },
    })
  }

  async accountExists(dto: AccountExistsSchemaDto) {
    const ret = await this.prisma.user.findFirstOrThrow({
      where: {
        username: dto.username,
        tenant: {
          name: dto.tenantName,
        },
      },
    })
    return ret
  }

  async findByUnionIdAndTenantId(dto: FindByUnionIdAndTenantIdSchemaDto) {
    const ret = await this.prisma.user.findFirst({
      where: {
        unionid: dto.unionid,
        tenantId: dto.tenantId,
      },
    })
    return ret
  }

  async registerByUnionId(dto: RegisterByUnionIdSchemaDto) {
    const user = await this.prisma.user.findFirst({
      where: {
        unionid: dto.unionid,
        tenantId: dto.tenantId,
      },
    })
    if (user) {
      throw new Error(`User exits with unionid: ${dto.unionid}`)
    }

    const aUser = await this.prisma.user.create({
      data: {
        username: dto.unionid, // 后续可以判断如果 unionid === username 则可以改动一次用户名
        hashedPassword: null,
        hashedRefreshToken: null,
        unionid: dto.unionid,
        tenantId: dto.tenantId,
      },
    })
    return aUser
  }
}

function generateVerificationCode() {
  let verificationCode = ''
  const digits = '0123456789'

  while (verificationCode.length < 6) {
    const randomDigit = digits[Math.floor(Math.random() * digits.length)]
    if (!verificationCode.includes(randomDigit)) {
      verificationCode += randomDigit
    }
  }

  return verificationCode
}
