import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import type { SchemaService } from '@flowda-projects/flowda-shared'
import { SchemaServiceSymbol } from '@flowda-projects/flowda-shared'
// todo: 改成 interface 去掉对 flowda-services 的依赖
import type { DynamicTableDefService } from '@flowda-projects/flowda-services'
import { DynamicTableDefServiceSymbol } from '@flowda-projects/flowda-services'

@injectable()
export class SchemaRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(SchemaServiceSymbol) private schemaService: SchemaService,
    @inject(DynamicTableDefServiceSymbol) private dynamicTableDef: DynamicTableDefService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(SchemaRouter.name)
  }

  schemaRouter = this.trpc.router({
    getSchema: this.trpc.procedure.input(z.undefined()).query(async ({ input }) => {
      // todo: 验证 & 权限控制放在哪里？
      // 之前 nestjs 可以放在 guard 上，但是用了 trpc 就不适合了，估计要放在 inversify，但是要能传递 context(inner)
      const zodSchema = this.schemaService.getSchema()
      const dynamicSchema = await this.dynamicTableDef.getSchema()
      const ret = Object.assign({}, zodSchema, dynamicSchema)
      return ret
    }),
  })
}
