import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import * as _ from 'radash'

@injectable()
export class HelloRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(HelloRouter.name)
  }

  helloRouter = this.trpc.router({
    createRoot: this.trpc.procedure.input(z.any()).query(async opts => {
      return {
        id: 'WorkspaceNodeId',
        name: 'WorkspaceNode',
        visible: false,
        children: [
          {
            id: '0',
            name: 'resources',
            selected: false,
            expanded: false,
            children: [],
          },
          {
            id: '1',
            name: 'workflows',
            selected: false,
            expanded: false,
            children: [],
          },
          {
            id: '2',
            name: 'reports',
            selected: false,
            expanded: false,
            children: [],
          },
        ],
      }
    }),

    resolveChildren: this.trpc.procedure
      .input(
        z.object({
          pid: z.string(),
        }),
      )
      .query(async opts => {
        const id = _.uid(4)
        return [
          {
            id: id,
            name: '服务器管理' + id,
            selected: false,
            uri: {
              scheme: 'resource',
              name: '服务器管理' + id,
            },
          },
        ]
      }),
  })
}
