import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import {
  accountExistsSchema,
  findByUnionIdAndTenantIdSchema,
  getTenantByNameSchema,
  registerByUnionIdSchema,
  UserService,
  verifyMobileSchema,
} from '@flowda-projects/flowda-services'
import { TenantSchema, UserSchema } from '@flowda-projects/prisma-flowda'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import * as db from '@prisma/client-flowda'

@injectable()
export class UserRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(UserService) private userService: UserService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserRouter.name)
  }

  userRouter = this.trpc.router({
    getTenant: this.trpc.procedure
      .input(z.object({ tid: z.number() }))
      .output(TenantSchema)
      .query(async ({ input }) => {
        return this.userService.getTenantInfo(input.tid)
      }),

    sendSmsVerifyCode: this.trpc.procedure.input(z.object({ mobile: z.string() })).mutation(async ({ input }) => {
      return this.userService.sendSmsVerifyCode(input.mobile)
    }),

    verifyMobile: this.trpc.procedure.input(verifyMobileSchema).mutation(async ({ input }) => {
      return this.userService.verifyMobile(input)
    }),

    accountExists: this.trpc.procedure.input(accountExistsSchema).query(async ({ input }) => {
      return this.userService.accountExists(input)
    }),

    findByUnionIdAndTenantId: this.trpc.procedure
      .input(findByUnionIdAndTenantIdSchema)
      .output(z.nullable(UserSchema))
      .query(async ({ input }) => {
        return this.userService.findByUnionIdAndTenantId(input)
      }),

    registerByUnionId: this.trpc.procedure
      .input(registerByUnionIdSchema)
      .output(UserSchema)
      .mutation(({ input }) => {
        return this.userService.registerByUnionId(input)
      }),

    getTenantByName: this.trpc.procedure
      .input(getTenantByNameSchema)
      .output(TenantSchema)
      .query(async ({ input }) => {
        return this.userService.getTenantByName(input)
      }),

    validate: this.trpc.procedure
      .input(
        z.object({
          username: z.string(),
          password: z.string(),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.userService.validate(input.username, input.password)
        return ret
      }),

    validateByEmail: this.trpc.procedure
      .input(
        z.object({
          email: z.string(),
          tenantId: z.number(),
          password: z.string(),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.userService.validateByEmail(input.tenantId, input.email, input.password)
        return ret
      }),

    findMany: this.trpc.procedure
      .input(
        z.object({
          userIds: z.array(z.number()),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.prisma.user.findMany({
          where: {
            id: {
              in: input.userIds,
            },
          },
        })
        return ret
      }),

    updateUserInfo: this.trpc.procedure
      .input(
        z.object({
          userId: z.number(),
          email: z.string().optional(),
          username: z.string().optional(),
          image: z.string().optional(),
        }),
      )
      .mutation(async ({ input }) => {
        const { username, email, image } = input
        return this.prisma.user.update({
          where: {
            id: input.userId,
          },
          data: {
            ...(username && { username }),
            ...(email && { email }),
            ...(image && { image }),
          },
        })
      }),

    findUnique: this.trpc.procedure
      .input(
        z.object({
          email: z.string().optional(),
          id: z.number().optional(),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.prisma.user.findUnique({
          where: input,
        })
        return ret
      }),
  })
}
